/* Set box-sizing to border-box for all elements */
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

/* Reset margins and paddings on most elements */
body,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
p,
pre,
blockquote,
figure,
hr {
  margin: 0;
  padding: 0; }

/* Removes discs from ul */
ul {
  list-style: none; }

/* Reset forms and buttons */
input,
textarea,
select,
button {
  color: inherit;
  font: inherit;
  letter-spacing: inherit; }

input,
textarea,
button {
  border: 1px solid gray; }

button {
  border-radius: 0;
  padding: 0.75em 1em; }

button * {
  pointer-events: none; }

/* Easy responsive for media elements */
embed,
iframe,
img,
object,
video {
  display: block;
  max-width: 100%; }

/* Useful table styles */
table {
  table-layout: fixed;
  width: 100%; }

/* Hidden */
[hidden] {
  display: none !important; }

/* noscript styles */
noscript {
  display: block;
  margin-bottom: 1em;
  margin-top: 1em; }

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  float: right;
  margin: 0 auto;
  padding: 0 1rem; }

.main-wrapper {
  max-width: 992px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  flex-flow: column wrap; }

.create-or-join-wrapper {
  flex: 1;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center; }

.sign-in-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column wrap;
  flex: 1; }

.room-wrapper {
  display: grid;
  grid-template-columns: 1fr 4fr; }

::-webkit-scrollbar {
  width: 12px; }

::-webkit-scrollbar-thumb {
  background: #6b6b6b;
  min-height: 50px; }

::-webkit-scrollbar-track {
  background: #f5f5f5;
  box-shadow: inset 0px 0px 10px 1px rgba(0, 0, 0, 0.1); }

.form__input {
  display: block;
  outline: none;
  border: none;
  appearance: none;
  background: transparent;
  height: 3.5rem;
  min-width: 17.5rem;
  font-family: inherit;
  line-height: 1.5;
  font-weight: 700;
  padding: 0 1.2rem;
  border-radius: 0.4rem;
  transition: all 150ms cubic-bezier(0.4, 0, 0.2, 1); }
  .form__input[type=number]::-webkit-inner-spin-button, .form__input input[type=number]::-webkit-outer-spin-button {
    appearance: none;
    margin: 0; }
  .form__input[type=number] {
    appearance: textfield;
    -moz-appearance: textfield; }
  .form__input:focus {
    box-shadow: inset 0 0 4px 2px rgba(0, 0, 0, 0.03), 0 0 0.5rem #161616; }

.form__input--primary {
  background-color: #161616;
  color: #f5f5f5; }

.form__lable {
  position: absolute;
  color: #6b6b6b;
  font-weight: 700;
  line-height: 1.35;
  top: 1rem;
  left: 1.2rem;
  cursor: text;
  transition: all 150ms cubic-bezier(0.4, 0, 0.2, 1); }

.form__input:focus + .form__lable,
.form__input:valid + .form__lable {
  font-size: 0.8em;
  font-weight: 400;
  top: -1.5rem;
  left: 0.4rem;
  cursor: default; }

.form__input-group {
  position: relative;
  margin-bottom: 1.5rem; }

.form__input--invalid {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0; }

.error-message {
  color: #FD3D39;
  width: 17.5rem;
  display: inline-block;
  padding: 0.6rem;
  border-bottom-left-radius: 0.4rem;
  border-bottom-right-radius: 0.4rem;
  background-color: rgba(253, 61, 57, 0.2); }

.error-message__transition-enter-active,
.error-message__transition-leave-active {
  transition: all 230ms cubic-bezier(0.4, 0, 0.2, 1); }

.error-message__transition-enter,
.error-message__transition-leave-to {
  opacity: 0; }

.btn {
  display: block;
  outline: none;
  border: none;
  appearance: none;
  cursor: pointer;
  background: transparent;
  height: 3.5rem;
  min-width: 17.5rem;
  font-family: inherit;
  line-height: 1.5;
  font-weight: 700;
  padding: 0 1.2rem;
  border-radius: 0.4rem; }

.btn--primary {
  background-color: #161616;
  color: #f5f5f5; }

.btn--secondary {
  border: 0.2rem solid #161616;
  text-transform: uppercase;
  transition: all 150ms cubic-bezier(0.4, 0, 0.2, 1); }
  .btn--secondary:hover, .btn--secondary:focus, .btn--secondary:active {
    box-shadow: inset 0 0 4px 2px rgba(0, 0, 0, 0.03), 0 0 0.3rem #161616; }

/* roboto-regular - latin */
