/* roboto-700 - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("./../assets/fonts/roboto-v19-latin-700.woff2") format("woff2"), url("./../assets/fonts/roboto-v19-latin-700.woff") format("woff"); }

