/* Miscellaneous
   ========================================================================== */
body {
  max-width: 100vw;
  min-height: 100vh;
  background-color: #f5f5f5;
  color: #161616;
  overflow-x: hidden;
  overflow-y: auto;
  scroll-behavior: smooth; }

#app {
